
.evaluateitem {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 18px 0;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.evaluateitem .left-content {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 28px;
          flex: 0 0 28px;
  width: 28px;
  height: 28px;
  border-radius: 50%;
  margin-right: 12px;
  overflow: hidden;
}
.evaluateitem .left-content img {
  display: inline-block;
  height: 28px;
  width: 28px;
  border-radius: 50%;
}
.evaluateitem .right-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.evaluateitem .right-content .top-content {
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.evaluateitem .right-content .top-content .user-name {
  color: rgb(7, 17, 27);
}
.evaluateitem .right-content .top-content .evaluate-date {
  font-weight: 200;
  float: right;
}
.evaluateitem .right-content .top-content .star-time-box{
  margin-top: 4px;
  font-size: 0;
  margin-bottom: 6px;
}
.evaluateitem .right-content .top-content .star-time-box .star{
  display: inline-block;
  vertical-align: top;
}
.evaluateitem .right-content .top-content .spend-time{
  font-size:10px;
  vertical-align: top;
  color: rgb(147, 153, 159);
  font-weight: 200;
  line-height: 12px;
}
.evaluateitem .center-content{
  font-size: 12px;
  color: rgb(7, 17, 27);
  line-height: 18px;
  margin-bottom: 8px;
}
.evaluateitem .bottom-content{
  font-size: 0px;
  color: rgb(183,187,193);
  line-height: 16px;
}
.evaluateitem .bottom-content .goods-label{
  display: inline-block;
  font-size: 12px;
  background-color: rgb(255, 255, 255);
  border-radius: 2px;
  border: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0 6px;
  margin-right: 8px;
  width: 90px;
  box-sizing: border-box;
  text-overflow: ellipsis;
  white-space:nowrap; overflow:hidden;
}



