
.index-footer {
  position: fixed;
  border-top: 1px solid #ccc;
  height: 50px;
  width: 100%;
  bottom: 0;
  left: 0;
  z-index: 1;
  background-color: rgba(255, 255, 255, 0.7)
}
.footer-tab {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.footer-tab-item {
  text-decoration: none;
  display: block;
  color: #333;
  -webkit-box-flex: 1;
      -ms-flex-positive: 1;
          flex-grow: 1;
  text-align: center;
}
.footer-tab .router-link-active {
  color: rgb(0, 150, 255);
}
.footer-icon {
  display: block;
  height: 20px;
  font-size: 20px;
  padding: 7px 0 2px;
}
.footer-tab-item-content {
  height: 20px;
  font-size: 14px;
  display: block;
}




