
.goods-content {
  position: absolute;
  top: 0px;
  bottom: 46px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  overflow: hidden;
}
.goods-left-side {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 80px;
          flex: 0 0 80px;
  width: 80px;
  background-color: #f3f5f7;
  color: #666;
}
.goods-left-side-list-item {
  display: table;
  height: 54px;
  width: 56px;
  padding: 0 12px;
  font-size: 0;
  line-height: 14px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  font-size: 12px;
}
.goods-left-side-list-item.on {
  background: #fff;
  border-left: 3px solid rgb(0, 150, 255);
}
.goods-left-side-list-item.on .text{
  color: #666;
  font-size: 14px;
  line-height: 16px;
  font-weight: bolder;
}
.goods-left-side-list-item .icon {
  display: inline-block;
  height: 14px;
  width: 10px;
  margin-right: 2px;
}
.goods-left-side-list-item .text {
  display: table-cell;
  width: 56px;
  font-weight: 200;
  vertical-align: middle;
}
.goods-right-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
}
.goods-item-list-title {
  height: 26px;
  line-height: 26px;
  background-color: #f3f5f7;
  border-left: 2px solid #d9dde1;
  font-size: 12px;
  padding-left: 14px;
}
.goods-item-list-content {
  padding: 0 18px;
}
.goods-item-list-content li {
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin: 18px 0;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.goods-item-list-content li:first-child {
  border-bottom: none;
}
.goods-item-list-content .item-icon {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 60px;
          flex: 0 0 60px;
  width: 60px;
  overflow: hidden;
  height: 60px;
  border-radius: 4px;
  margin-right: 10px;
}
.goods-item-list-content .item-icon img {
  height: 60px;
  width: 60px;
}
.goods-item-list-content .item-description {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.goods-item-list-content .item-description .item-title {
  margin-top: 2px;
  font-size: 16px;
  line-height: 14px;
  color: rgb(7, 17, 27);
}
.goods-item-list-content .item-description .item-name {
  font-size: 10px;
  color: rgb(147, 153, 159);
  line-height: 12px;
  margin: 8px 0;
}
.goods-item-list-content .item-description .price {
  font-size: 10px;
  color: rgb(240, 20, 20);
  font-weight: normal;
  line-height: 24px;
}
.goods-item-list-content .item-description .price .now {
  display: inline-block;
  font-size: 14px;
  font-weight: 700;
  vertical-align: middle;
}
.goods-item-list-content .item-description .price .now i {
  font-size: 10px;
  font-weight: normal;
}
.goods-item-list-content .item-description .price .old {
  display: inline-block;
  font-size: 10px;
  color: rgb(147, 153, 159);
  font-weight: 700;
  text-decoration: line-through;
}
.goods-item-list-content .item-description .sell-number {
  font-size: 10px;
  color: rgb(147, 153, 159);
  margin-top: 8px;
  line-height: 12px;
  margin-bottom: 4px;
}




