
.cartcontrol {
  font-size: 0;
  height: 24px;
}
.cart-decrease {
  display: inline-block;
  line-height: 24px;
  vertical-align: top;
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.cartcontrol .iconfont {
  color: rgb(0, 150, 255);
  font-size: 23px;
  line-height: 24px;
}
.inner {
  -webkit-transition: all 0.4s linear;
  transition: all 0.4s linear;
  -webkit-transform: rotate(0);
          transform: rotate(0);
}
.cart-count {
  font-size: 10px;
  color: rgb(147, 153, 159);
  display: inline-block;
  width: 24px;
  text-align: center;
  line-height: 24px;
  vertical-align: top;
}
.cart-add {
  vertical-align: top;
  display: inline-block;
  color: rgb(0, 150, 255);
  line-height: 24px;
}
.move-enter-active,
.move-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.move-enter,
.move-leave-active {
  opacity: 0;
  -webkit-transform: translate3d(22px, 0, 0);
          transform: translate3d(22px, 0, 0);
}
.move-enter .inner,
.move-leave-active .inner {
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}




