
.login-password {
  background-color: #f5f5f5;
  height: 100%;
}
.login-content input {
  padding: 0 10px;
  width: 100%;
  height: 40px;
  border: none;
  font-size: 14px;
  outline: none;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.mobile-box {
  position: relative;
}
.mobile-box input {
  border-bottom: none;
}
.password{
  position: relative;
}
.password span.icon-yanjing{
  display: inline-block;
  font-size: 20px;
  position: absolute;
  right: 20px;
  top: 50%;
  color: #ddd;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.password span.show{
  color: rgb(0, 150, 255);
}
.login-btn {
  height: 40px;
  border: none;
  width: 100%;
  background: #4cd964;
  font-size: 16px;
  color: #fff;
  box-sizing: border-box;
  display: block;
  border-radius: 4px;
  outline: none;
}
.user-service{
  color: #4b9dea;
}



