
.shopcart {
  position: fixed;
  bottom: 0;
  width: 100%;
  height: 46px;
  line-height: 46px;
  z-index: 50;
}
.food-total-count {
  background: #f31515;
  display: inline-block;
  width: 20px;
  height: 12px;
  position: absolute;
  border-radius: 6px;
  color: #fff;
  font-size: 8px;
  line-height: 12px;
  font-weight: normal;
}
.shopcart .shopcart-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #141d27;
  color: rgba(255, 255, 255, 0.4);
}
.shopcart .icon-shopcart {
  position: absolute;
  top: -10px;
  left: 12px;
  display: inline-block;
  width: 50px;
  height: 50px;
  text-align: center;
  border-radius: 50%;
  background-color: #141d27;
}
.shopcart .icon-shopcart:before {
  display: inline-block;
  position: absolute;
  width: 40px;
  height: 40px;
  line-height: 40px;
  border-radius: 50%;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background: rgba(255, 255, 255, 0.2);
  font-size: 25px;
}
.shopcart .highlight-cart {
  color: #fff;
}
.shopcart .highlight-cart:before {
  background: rgb(0, 150, 255);
}
.shopcart .shopcart-totalprice {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 115px;
          flex: 0 0 115px;
  width: 115px;
  padding-left: 60px;
  text-align: center;
  font-size: 16px;
  color: rgba(255, 255, 255, 0.4);
  font-weight: 700;
  box-sizing: border-box;
}
.shopcart .highlight-price {
  color: #fff;
}
.shopcart .shopcart-send-money {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  font-size: 10px;
  padding-left: 12px;
  margin-left: 12px;
  line-height: 22px;
  height: 22px;
  margin-top: 12px;
  border-left: 1px solid rgba(255, 255, 255, 0.1);
}
.shopcart .littleprice {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 110px;
          flex: 0 0 110px;
  width: 110px;
  font-size: 12px;
  font-weight: 700;
  background: rgba(255, 255, 255, 0.2);
  text-align: center;
}
.shopcart .cansend {
  background: #00b43c;
  color: #fff;
}
.shopcart-list {
  width: 100%;
  background-color: #fff;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  -webkit-transform: translate3d(0, -100%, 0);
          transform: translate3d(0, -100%, 0)
}
.fold-enter-active,
.fold-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fold-enter,
.fold-leave-active {
  -webkit-transform: translate3d(0, 0, 0);
          transform: translate3d(0, 0, 0);
}
.shopcart-list .list-title {
  height: 40px;
  background-color: #f3f4f5;
  padding: 0 18px;
  line-height: 40px;
  font-size: 14px;
  font-weight: 200;
  color: rgb(7, 17, 27);
}
.shopcart-list .shopcart-list-box {
  max-height: 217px;
  overflow: hidden;
  padding: 0 18px;
}
.shopcart-list .list-title .empty {
  font-size: 12px;
  color: rgb(0, 160, 200);
  float: right;
}
.shopcart-list .shopcart-list-item {
  line-height: 48px;
  border-bottom: 1px solid #d7d7d7;
  padding: 0 8px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  position: relative;
}
.shopcart-list .shopcart-list-item .name {
  color: rgb(7, 17, 27);
  -webkit-box-flex: 0;
      -ms-flex: 0 0 150px;
          flex: 0 0 150px;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
  font-size: 14px;
}
.shopcart-list .shopcart-list-box .cartcontrol-box {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 75px;
          flex: 0 0 75px;
}
.shopcart-list .shopcart-list-item .price {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: rgb(240, 20, 20);
  font-size: 14px;
  padding-right: 64px;
  text-align: right;
}
.shopcart-list .shopcart-list-item .price .price-icon {
  font-size: 10px;
}
.cartcontrol-box {
  position: absolute;
  bottom: 12px;
  right: 0;
}
.mask {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -2;
  opacity: 1;
  background: rgba(7, 17, 27, 0.6);
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: all 0.5s;
  transition: all 0.5s;
}
.fade-enter,
.fade-leave-active {
  opacity: 0;
  background: rgba(7, 17, 27, 0)
}




