
.personcenter {
  width: 100%;
  background: #f4f4f4;
}
.container{
   background-color:#fff;
   margin-top: 20px;
}
.account{
 display: -webkit-box;
 display: -ms-flexbox;
 display: flex;
 height: 110px;
 background-color: rgb(0,150,255);
 padding-top: 45px;
}
.account .person-icon{
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
  height: 110px;
}
.account .person-icon img{
  width: 70px;
  height: 70px;
  border-radius: 50%;
  margin: 20px 10px 0;
}
.account .login-box{
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  color: #fff;
  padding-top: 30px;
}
.account .login-box .login-signin{
  font-size: 20px;
  line-height: 24px;
  margin-bottom: 10px;
}
.personcenter .person-info{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background: #fff;
  text-align: center;
  border-bottom: 1px solid #d7d7d7;
  font-size: 12px;
}
.personcenter .person-info div{
  padding: 20px;
  box-sizing: border-box;
  border-right: 1px solid #d7d7d7;
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.personcenter .person-info div:last-child{
  border-right: none;
}
.personcenter .person-info .iconfont{
  font-size: 24px;
  margin-bottom: 10px;
  display: block;
}
.personcenter .person-info .balance .iconfont{
  color: #ff9900;
}
.personcenter .person-info .discount .iconfont{
  color: #ff5f3e;
}
.personcenter .person-info .integral .iconfont{
  color: #6ac20b;
}
.personcenter .details{
  border-top: 1px solid #ccc;
  border-bottom: 1px solid #ccc;
  padding: 0 15px;
}
.personcenter .details .details-item{
  position: relative;
  height: 40px;
  line-height: 40px;
  font-size: 16px;
  border-bottom: 1px solid #ccc;
}
.personcenter .details .details-item:last-child{
  border: none;
}
.personcenter .details .details-item .iconfont{
  font-size: 16px;
  padding:0 5px;
}
.personcenter .details .details-item .icon-arrow{
  padding: 0;
  font-size: 20px;
  display: inline-block;
  position: absolute;
  right: 0;
  top: 50%;
  margin-top: -20px;
}
.login-description{
  font-size: 14px;
}



