
.addaddress {
  height: 100%;
}
.addaddress-content {
  padding-top: 45px;
}
.addaddress-content .input-box:first-child {
  margin-top: 10px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.addaddress-content .input-box {
  background: #fff;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0 10px;
}
.addaddress-content .title {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
  height: 40px;
  line-height: 40px;
}
.addaddress-content .left-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.addaddress-content .left-content input[type=text] {
  display: block;
  height: 40px;
  border: none;
  line-height: 40px;
  padding: 0;
  outline: none;
  width: 100%;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.addaddress-content .left-content input[type=radio] {
  display: none;
}
.addaddress-content .left-content label {
  display: inline-block;
  padding: 5px 0;
  width: 60px;
  text-align: center;
  border: 1px solid rgba(7, 17, 27, 0.1);
  margin: 6px 0;
  color: #333;
  border-radius: 2px;
  margin-right: 10px;
}
.addaddress-content .left-content label.active {
  color: rgb(0, 150, 255);
  border: 1px solid rgb(0, 150, 255);
}
.btn {
  height: 40px;
  border: none;
  width: 100%;
  background: #4cd964;
  font-size: 16px;
  color: #fff;
  box-sizing: border-box;
  display: block;
  border-radius: 4px;
  outline: none;
}




