
.comfirmorder {
   height: 100%;
}
.comfirmorder .comfirmorder-content {
   padding-top: 45px;
}
.select-address {
   background: #fff;
   padding: 10px 30px;
   position: relative;
}
.select-address .address-content .top{
   padding-bottom: 5px;
}
.select-address .address-content .name{
   font-size: 16px;
}
.select-address .address-content .sex,.select-address .address-content .phonenumber{
   font-size: 14px;
}
.select-address .address-content .bottom{
   font-size: 12px;
   line-height: 18px;
}
.select-address .address-content .bottom .label{
   color: #fff;
   background: rgb(0, 150, 255);
   font-size: 10px;
   padding: 0 5px;
   margin-right: 10px;
   border-radius: 2px;
}
.select-address .iconfont {
   position: absolute;
}
.select-address .icon-dizhi {
   color: rgb(0, 150, 255);
   left: 10px;
   -webkit-transform: translateY(-50%);
           transform: translateY(-50%);
   top: 50%;
}
.select-address .icon-arrow {
   right: 10px;
   -webkit-transform: translateY(-50%);
           transform: translateY(-50%);
   top: 50%;
}
.comfirmorder .send-time {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   border-left: 3px solid rgb(0, 150, 255);
   border-top: 1px solid rgba(7, 17, 27, 0.1);
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
   margin-top: 10px;
   padding: 8px 0;
   background: #fff;
}
.comfirmorder .send-time .time {
   -webkit-box-flex: 0;
       -ms-flex: 0 0 100px;
           flex: 0 0 100px;
   line-height: 60px;
   padding-left: 10px;
}
.comfirmorder .send-time .desciprtion {
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
   text-align: right;
   padding-right: 10px;
}
.comfirmorder .send-time .desciprtion div {
   line-height: 30px;
   color: #ccc;
   font-size: 14px;
}
.comfirmorder .send-time .desciprtion .top {
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.comfirmorder .pay-method {
   background: #fff;
   margin-top: 10px;
   height: 40px;
   line-height: 40px;
   padding: 0 10px;
   position: relative;
}
.comfirmorder .pay-method .method {
   font-size: 12px;
   color: #666;
   float: right;
   padding-right: 16px;
}
.comfirmorder .pay-method .iconfont {
   position: absolute;
   right: 10px;
   font-size: 12px;
}
.order-detail {
   background: #fff;
   margin-top: 10px;
}
.order-detail .shop-name {
   height: 40px;
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
   line-height: 40px;
   box-sizing: border-box;
   color: #666;
   padding-left: 10px;
}
.order-detail .shop-name img {
   display: inline-block;
   height: 18px;
   width: 18px;
   vertical-align: middle;
   margin-right: 10px;
}
.order-detail .order-food-list {
   padding: 0 10px;
}
.order-detail .order-food-list .order-food-list-item {
   height: 40px;
   line-height: 40px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
}
.order-detail .order-food-list .order-food-list-item .food-number {
   -webkit-box-flex: 0;
       -ms-flex: 0 0 30px;
           flex: 0 0 30px;
   text-align: right;
   font-size: 14px;
   color: #666;
}
.order-detail .order-food-list .order-food-list-item .food-total-price {
   -webkit-box-flex: 0;
       -ms-flex: 0 0 50px;
           flex: 0 0 50px;
   text-align: right;
}
.order-detail .order-food-list .order-food-list-item .food-name {
   overflow: hidden;
   text-overflow: ellipsis;
   white-space: nowrap;
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
}
.box-fee,
 .send-fee {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   background: #fff;
   height: 40px;
   line-height: 40px;
   padding: 0 10px;
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
   box-sizing: border-box;
}
.box-fee .text {
   color: #666;
}
.box-fee .box-price,
 .send-fee .send-money {
   -webkit-box-flex: 0;
       -ms-flex: 0 0 50px;
           flex: 0 0 50px;
   text-align: right;
}
.box-fee .text,
 .send-fee .text {
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
}
.remark {
   margin-top: 10px;
   background: #fff;
}
.remark .item {
   height: 40px;
   line-height: 40px;
   box-sizing: border-box;
   border-bottom: 1px solid rgba(7, 17, 27, 0.1);
   padding: 0 10px;
   position: relative;
}
.remark .item span {
   display: inline-block;
   position: absolute;
   right: 10px;
   color: #666;
   font-size: 12px;
}
.remark .item span .iconfont {
   font-size: 12px;
}
.comfirmorder .bottom-content {
   height: 45px;
   line-height: 45px;
   background: #222;
   position: fixed;
   bottom: 0;
   right: 0;
   left: 0;
   color: #fff;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
}
.comfirmorder .bottom-content .wait-pay-money {
   -webkit-box-flex: 1;
       -ms-flex: 1;
           flex: 1;
   padding-left: 10px;
}
.comfirmorder .bottom-content .submit-order {
   -webkit-box-flex: 0;
       -ms-flex: 0 0 120px;
           flex: 0 0 120px;
   background: #00b43c;
   text-align: center;
}




