
.seller {
  position: absolute;
  top: 0;
  bottom: 0;
  background: #f3f5f7;
  overflow: hidden;
  width: 100%;
}
.seller .seller-top {
  background-color: #fff;
  padding: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .seller-name {
  font-size: 14px;
  color: rgb(7, 17, 27);
  line-height: 14px;
  margin-bottom: 8px;
}
.seller .star-sell-num {
  font-size: 0;
  color: rgb(77, 85, 93);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  padding-bottom: 18px;
}
.star-sell-num .star {
  display: inline-block;
  vertical-align: top;
}
.seller .star-sell-num .sell-count {
  vertical-align: top;
  display: inline-block;
  line-height: 18px;
  font-size: 10px;
  margin-left: 20px;
}
.sell-detail {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding-top: 18px;
  width: 100%;
  text-align: center;
}
.sell-detail span {
  font-size: 10px;
  color: rgb(147, 153, 159);
}
.sell-detail p {
  margin-top: 4px;
  font-size: 20px;
  color: rgb(7, 17, 27);
  line-height: 24px;
  font-weight: 200;
}
.sell-detail p span.small-font {
  color: rgb(7, 17, 27);
}
.sell-detail .can-send {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.sell-detail .sell-send {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-left: 1px solid rgba(7, 17, 27, 0.1);
}
.sell-detail .send-time {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-left: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .notice-activity,
.seller .seller-infomation {
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  margin: 18px 0;
  padding: 18px;
  background-color: #fff;
}
.seller .notice-activity .title,
.seller .seller-infomation .title {
  font-size: 14px;
  color: rgb(7, 17, 27);
  margin-bottom: 8px;
}
.seller .notice-activity p {
  font-size: 10px;
  font-weight: 200;
  color: rgb(240, 20, 220);
  line-height: 24px;
  padding-left: 12px;
}
.seller .notice-activity ul li,
.seller .seller-infomation ul li {
  padding: 12px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
}
.seller .notice-activity .activity-item,
.seller .seller-infomation .activity-item {
  display: inline-block;
  height: 16px;
  width: 100%;
  line-height: 20px;
  font-size: 12px;
  font-weight: 200;
  color: rgb(7, 17, 27);
  margin: 0;
}
.seller .notice-activity .activity-item .icon .seller .seller-infomation .activity-item .icon {
  display: inline-block;
  height: 16px;
  width: 16px;
}




