
.payonline {
  height: 100%;
}
.payonline .payonline-content {
  padding-top: 45px;
}
.payonline .payonline-content .pay-time {
  background: #fff;
  text-align: center;
  padding: 0 10px;
}
.payonline .payonline-content .pay-time p {
  font-size: 12px;
  color: #333;
  margin-bottom: 15px;
  padding-top: 15px;
}
.payonline-content .pay-time .time {
  padding-bottom: 15px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.payonline-content .pay-detail {
  height: 40px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  line-height: 40px;
}
.payonline-content .pay-detail .detail {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 30px;
          flex: 0 0 30px;
  font-size: 12px;
}
.payonline-content .pay-detail .total-price {
  color: #ff6000;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 100px;
          flex: 0 0 100px;
}
.payonline-content .pay-detail .shop-name {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  padding-right: 10px;
}
.payonline-content .pay-method {
  background: #fff;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.payonline-content .pay-method h2 {
  height: 40px;
  line-height: 40px;
  padding: 0 10px;
}
.payonline-content .pay-method .pay-method-item {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  height: 60px;
  line-height: 60px;
  margin-left: 10px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.payonline-content .pay-method .pay-method-item .iconfont {
  font-size: 24px;
  -webkit-box-flex: 0;
      -ms-flex: 0 0 40px;
          flex: 0 0 40px;
}
.payonline-content .pay-method .pay-method-item .icon-z-alipay {
  color: #00a1e9;
}
.payonline-content .pay-method .pay-method-item .icon-radio {
  color: #ccc;
}
.payonline-content .pay-method .pay-method-item .icon-radio.active,
.payonline-content .pay-method .pay-method-item .icon-weixin-pay {
  color: #00ba61;
}
.payonline-content .pay-method .pay-method-item .icon-yinlianzhifu {
  color: rgb(0, 150, 255);
}
.payonline-content .pay-method .pay-method-item .name {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
}
.payonline-content .pay-method .show-more {
  height: 40px;
  line-height: 40px;
  color: #ccc;
  text-align: center;
  font-size: 14px;
}
.payonline-content .pay-method .show-more .iconfont {
  font-size: 12px;
  display: inline-block;
  margin-left: 10px;
}
.payonline .bottom-content {
  position: fixed;
  background: #4cd964;
  bottom: 0;
  left: 0;
  right: 0;
  height: 45px;
  line-height: 45px;
  text-align: center;
  color: #fff;
}




