
.evaluate {
  position: absolute;
  top: 0;
  bottom: 0;
  background: #f3f5f7;
  overflow: hidden;
  width: 100%;
}
.top-score {
  padding: 18px 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
  margin-bottom: 18px;
  background: #fff;
}
.top-score .left-content {
  -webkit-box-flex: 0;
      -ms-flex: 0 0 135px;
          flex: 0 0 135px;
  width: 135px;
  text-align: center;
}
.top-score .left-content .score {
  font-size: 24px;
  color: rgb(255, 153, 0);
  line-height: 28px;
  display: block;
  margin-bottom: 6px;
}
.top-score .left-content .total-evaluate {
  display: block;
  font-size: 12px;
  line-height: 12px;
  color: rgb(7, 17, 27);
  margin-bottom: 8px;
}
.top-score .left-content .high-rate {
  font-size: 10px;
  line-height: 10px;
  color: rgb(147, 153, 159);
}
.top-score .right-content {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  border-left: 1px solid rgba(7, 17, 27, 0.1);
  padding: 0 24px;
}
.top-score .right-content .star {
  display: inline-block;
  vertical-align: top;
}
.top-score .right-content .service,
.top-score .right-content .goods-evaluate,
.top-score .right-content .send-time {
  font-size: 0px;
  color: rgb(7, 17, 27);
  margin-bottom: 8px;
}
.top-score .right-content .send-time .title {
  vertical-align: top;
  display: inline-block;
  font-size: 12px;
  line-height: 18px;
  margin-right: 6px;
}
.top-score .right-content .score {
  vertical-align: top;
  display: inline-block;
  font-size: 16px;
  line-height: 18px;
  margin-left: 10px;
  color: rgb(255, 153, 0);
}
.top-score .right-content .send-time {
  margin-bottom: 0;
}
.top-score .right-content .send-time .time {
  color: rgb(147, 153, 159);
  display: inline-block;
  font-size: 12px;
  line-height: 18px;
}
.evaluate-bottom {
  padding: 18px;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  background: #fff;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.evaluate-bottom-label {
  font-size: 0;
  padding-bottom: 18px;
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.evaluate-bottom-label .label-item {
  display: inline-block;
  line-height: 32px;
  padding: 0 12px;
  font-size: 12px;
  border-radius: 2px;
  margin-left: 8px;
}
.evaluate-bottom-label .label-item .number {
  font-size: 8px;
  padding-left: 4px;
}
.evaluate-bottom-label .all {
  background: rgb(0, 160, 220);
  color: #fff;
}
.evaluate-bottom-label .satisfaction {
  background: rgba(0, 160, 220, 0.2);
  color: rgb(77, 85, 93);
}
.evaluate-bottom-label .dissatisfaction {
  background: rgba(77, 85, 93, 0.2);
  color: #5b6269;
}




