
.login-phonenumber {
  background-color: #f5f5f5;
  height: 100%;
}
.login-content input {
  padding: 0 10px;
  width: 100%;
  height: 40px;
  border: none;
  font-size: 14px;
  outline: none;
  border-top: 1px solid rgba(7, 17, 27, 0.1);
  border-bottom: 1px solid rgba(7, 17, 27, 0.1);
}
.mobile-box {
  position: relative;
}
.mobile-box input {
  border-bottom: none;
}
.mobile-box .send-code {
  position: absolute;
  right: 10px;
  display: inline-block;
  height: 34px;
  background: #cccccc;
  line-height: 34px;
  font-size: 14px;
  width: 100px;
  text-align: center;
  color: #fff;
  top: 50%;
  border-radius: 4px;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.mobile-box .send-code.active {
  background-color: #0097ff;
}
.tip {
  color: #9b9b9b;
  font-size: 10px;
  padding: 10px;
  line-height: 14px;
}
.login-btn {
  height: 40px;
  border: none;
  width: 100%;
  background: #4cd964;
  font-size: 16px;
  color: #fff;
  box-sizing: border-box;
  display: block;
  border-radius: 4px;
  outline: none;
}
.user-service{
  color: #4b9dea;
}



